body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-roboto: Roboto;
  --font-lato: Lato;
  --font-manrope: Manrope;
  --font-inter: Inter;
  --font-dm-sans: "DM Sans";

  /* font sizes */
  --font-size-mini: 15px;
  --font-size-sm: 14px;
  --font-size-3xl: 22px;
  --font-size-base: 16px;
  --font-size-7xl: 26px;
  --font-size-lg: 18px;
  --font-size-lgi: 19px;
  --font-size-31xl: 50px;
  --font-size-xl: 20px;
  --font-size-smi: 13px;
  --font-size-56xl: 75px;
  --font-size-mid: 17px;
  --font-size-13xl: 32px;
  --font-size-6xl: 25px;

  /* Colors */
  --color-white: #fff;
  --color-forestgreen-100: #57b33e;
  --color-darkslategray-100: #484848;
  --color-darkslategray-200: #333;
  --color-gray-100: #fafafa;
  --color-gray-200: #1f242c;
  --color-gray-300: #040404;
  --color-gray-400: rgba(255, 255, 255, 0);
  --color-gray-500: rgba(255, 255, 255, 0.2);
  --color-gray-600: rgba(255, 255, 255, 0.15);
  --color-dimgray-100: #5f5f5f;
  --color-coral: #ff7029;
  --color-whitesmoke-100: #eaeaea;
  --color-mintcream-100: #f4faf6;
  --color-mintcream-200: #eff6ed;
  --color-black: #000;
  --color-green: #00820b;
  --color-lightgreen: #81d489;
  --color-gainsboro: #dcdcdc;

  /* Gaps */
  --gap-33xl: 52px;
  --gap-lgi: 19px;
  --gap-11xl: 30px;
  --gap-2xs: 11px;
  --gap-31xl: 50px;
  --gap-4xs: 9px;
  --gap-13xl: 32px;
  --gap-base: 16px;
  --gap-21xl: 40px;
  --gap-3xl: 22px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-sm: 14px;
  --padding-13xl: 32px;
  --padding-mini: 15px;
  --padding-base: 16px;
  --padding-mid-5: 17.5px;
  --padding-37xl: 56px;
  --padding-4xs: 9px;
  --padding-3xs: 10px;
  --padding-66xl: 85px;
  --padding-11xl: 30px;
  --padding-12xs: 1px;
  --padding-5xs: 8px;
  --padding-31xl: 50px;
  --padding-12xl: 31px;
  --padding-7xl: 26px;
  --padding-12xs-1: 0.1px;
  --padding-141xl: 160px;
  --padding-14xl: 33px;
  --padding-5xl: 24px;
  --padding-13xl-3: 32.3px;
  --padding-3xl: 22px;
  --padding-23xl: 42px;
  --padding-xl: 20px;
  --padding-10xs: 3px;
  --padding-smi: 13px;
  --padding-2xl-5: 21.5px;

  /* border radiuses */
  --br-xl: 20px;
  --br-33xl: 52px;
  --br-25xl: 44px;
  --br-lgi-5: 19.5px;
  --br-10xs: 3px;
  --br-mid: 17px;
}
